body{
  background-color: rgb(54, 54, 129);
  color:white;
  border:0;
  font-size: 15px;
}
.content {
  background-color:#eee !important;
   color: black;
   border-radius:5px;
   margin:auto !important;
   padding: 15px;
   width: 90%;
   margin: auto;
}
.businesscontent {
  background-color:#eee !important;
   color: black;
   border-radius:5px;
   margin:auto !important;
   padding: 15px;
   width: 50%;
   margin: auto;
}
.signupcontent {
  background-color:#eee !important;
   color: black;
   border-radius:5px;
   margin:auto !important;
   padding: 15px;
   width: 40%;
   margin: auto;
   margin-top:10% !important;
}
#field {
  border: 1px solid #ddd;
  border-radius: 3px;
  height: 35px;
  float: right;
  width: 60%;
}
label {
  font-weight: normal;
}
.search {
  float:right;
  padding-right: 3%;}

#name-row {
  width:50px;}

#row {
  width:25px;}

#search {
  border-radius:2px;
  height: 30px;
  padding: 4px;
  border: 1px solid grey}

#search-icon {
  border-radius:2px;
  height: 30px;
  padding-top: 2px;
  border: 1px solid grey}

#filter {
  width:130px;
  float:right;}

#root{padding:1%}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



